import React from "react";
import classes from './header.module.css';
import logo from "../../img/icons/pngaaa.com-2507897.png";
import Navbar from "./Navbar/Navbar";
import {useDispatch, useSelector} from "react-redux";
import {LogoutThunkCreator} from "../../redux/authReducer";
import Navblock from "./Navblock/Navblock";
import LoginBlock from "./LoginBlock";
import {AppStateType} from "../../redux/store";
import {selectedIsAuth, selectedIsLogin} from "../../redux/authSelectors";


type ownPropsType={}

export const Header:React.FC<ownPropsType> = () =>{

    const menuItems = useSelector((state:AppStateType) => state.headerMenu.menuItems);
    const isAuth = useSelector(selectedIsAuth);
    const login = useSelector(selectedIsLogin);
    const dispatch = useDispatch();

    const logout = ()=>{
        dispatch(LogoutThunkCreator());
    };

    return(
        <div className={classes.g}>
        <div className={classes.header}>
            <div className={classes.img}>
                <img src={logo} alt="logotype" />
             </div>

            <div className={classes.nav}>
                <div className={classes.quote}>
                   <i>"Не волнуйтесь, если что-то не работает. Если бы всё работало, вас бы уволили..." </i>
                </div>
                    <Navbar menuItems={menuItems}/>
                     <Navblock isAuth={isAuth}
                               login={login}
                               LogoutThunkCreator={logout}
                     />
            </div>

        </div>
            <div className={classes.loginBlock}>
                <LoginBlock isAuth={isAuth}
                          login={login}
                          LogoutThunkCreator={logout}
                />
            </div>
</div>
    );
};



