import React, {useEffect, useState} from "react";
import { UserOutlined } from '@ant-design/icons';
import classes from "./chat.module.css";
import {Avatar} from "antd";
import {Form, Formik} from "formik";
import FormikControl from "../Formik/FormikControl";


  type ChatMessageType = {
    message: string
    photo:   string
    userId: number
    userName: string
}

export const Chat:React.FC = () =>{
    const [ws, setWs] = useState<WebSocket | null>(null);
    //создаем канал коммуникации по web socket и получаем сообщения в режиме online
    //записываем в state
    useEffect(()=>{
        let newSocket:WebSocket;
        const closeHandler = () =>{
            console.log('closes');
            setTimeout(createChannel(),3000);
        };
        const createChannel =() => {
                newSocket?.removeEventListener('close', closeHandler);
                newSocket?.close();

            newSocket =  new WebSocket("wss://social-network.samuraijs.com/handlers/ChatHandler.ashx\n");
            //ws? если не  null
            newSocket?.addEventListener('close', closeHandler);
            setWs(newSocket);
        };
        createChannel();

        //cleanup
        return () => {
            newSocket.removeEventListener('close', closeHandler);
            newSocket.close();
        }
    },[]);




    return <div className={classes.chat_container}>
        <ChatMessage ws = {ws}/>
        <ChatForm ws = {ws}/>
    </div>
};


const ChatMessage:React.FC<{ws:WebSocket | null}> = ({ws}) =>{

    const [messages, setMessages] = useState<ChatMessageType[]>([]);

    useEffect(()=>{
        let messageHandler = (event:MessageEvent) =>{
            let newMessage =  JSON.parse(event.data);
            setMessages((prevMessage) => [...prevMessage, ...newMessage]);
        };

        //подписываемся на событие в websocket канале
        ws?.addEventListener("message", messageHandler);
        //cleanup
        return () => {
            ws?.removeEventListener('message', messageHandler);
        }
    },[ws]);

    return <div className={classes.block_message}>
        {messages.map((m, index) =>
            <Message key={index} msg={m}/>
        )}
    </div>
};


const Message:React.FC<{msg:ChatMessageType}> = ({msg}) =>{
    return <div className={classes.chat_mess} >
        {msg.photo!==""
            ?<img src={msg.photo} alt=""/>
            :<Avatar className={classes.avatar_style} alt={'avatar'}  icon={<UserOutlined />} />
        }
        <span>{msg.userName}</span>
        <p>{msg.message}</p>
        <hr/>
    </div>
};


const ChatForm:React.FC<{ws:WebSocket | null}> = ({ws}) =>{
    const [ready, setReady] = useState<'pending' | 'ready'>('pending');

    const  initialValues= {
        message:''
    };
    type initialValuesType  = typeof initialValues;

    useEffect(()=>{
        let openHandler = () => {
            setReady('ready');
        };
        ws?.addEventListener('open',openHandler);
        return () => {
            ws?.removeEventListener('open',openHandler)
        }
    },[ws]);

    const onSubmit=( values:any, onSubmitProps:any) =>{
        ws?.send(values.message);
        onSubmitProps.resetForm()
    };
    return <div className={classes.chat_form}>
        <Formik
            enableReinitialize
            initialValues={initialValues}
            onSubmit={onSubmit}>
            {
                formik=><Form className={classes.form__formSearch}>
                    <FormikControl control='textarea'  label='Message' name='message'/>
                    <button className={classes.form__button} type='submit' disabled={ws === null || ready !== 'ready'}>Отправить</button>
                </Form>
            }
        </Formik>
    </div>
};

