import {resultCodeCaptchaEnum, resultCodeEnum} from "../api/api";
import {FormAction, stopSubmit} from "redux-form";
import { InferActionType, BaseThuncType} from "./store";
import {ChatMessageType} from "../api/chat_api";

//процессит текущего пользователя

let initialState = {
    messages:[] as ChatMessageType[]
};

type ActionsType= InferActionType<typeof actionsReducer>;
type thuncType = BaseThuncType<ActionsType| FormAction>

const chatReducer =  (state = initialState, action:ActionsType) => {
    switch (action.type) {
        case    "SET_MESSAGES":
            return {
                ...state,
                messages: [...state.messages, ...action.data.messages],
            };
        default:
            return state;
    }
};

export const actionsReducer={
    setMessages: (messages:ChatMessageType[]) => ({
        type: "SET_MESSAGES", data: {messages}
    } as const)
};


//thunk
export  const getMessages = ():thuncType => async (dispatch) => {

}
export default chatReducer;