import React, {useEffect} from "react";
import classes from './AllUsers.module.css';
import Preloader from "../Preloader/Preloader";
import Pagination from "./Pagination";
import OneUser from "./OneUser";
import {SearchFormik} from "../Formik/SearchFormik/SearchFormik";
import {filterType, followThunkCreator, getUsersThunkCreator, unFollowThunkCreator} from "../../redux/usersReducer";
import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentPage,
    getIsFetching, getIsFollowingProgress,
    getPageSize,
    getUsers,
    getUsersCount,
    getUsersFilter
} from "../../redux/usersSelectors";
import { useHistory } from "react-router-dom";
import * as queryString from "querystring";

type counterPropsType={
    userCount:number
}
type propsType ={

}

let Counter:React.FC<counterPropsType> = ({userCount}) =>{
    return  (
        <div className={classes.counterUsers}>
            <span className={classes.text}>всего пользователей</span>
            <span className={classes.numbers}>{userCount}</span>
         </div>
    )
};

export const AllUsers:React.FC<propsType> = () =>{

// hook useSelector принимает selectors (фукции достающие из state данные)
    const users = useSelector(getUsers);
    const isFollowingProgress = useSelector(getIsFollowingProgress);
    const  userCount = useSelector(getUsersCount);
    const  currentPage = useSelector(getCurrentPage);
    const pageSize = useSelector(getPageSize);
    const filter = useSelector(getUsersFilter);
    const isFetching = useSelector(getIsFetching);
    const dispatch = useDispatch();
    const history = useHistory();

    useEffect(()=>{
        //считывание с адресной строки браузера
        const {search} = history.location;//достаем из location  search который выглядит примерно так ?term=&friend=undefined&page=1
        const parsed = queryString.parse(search.substr(1));//получаем объект (substr(1)- избавляемся от первого знака ?)
        let page = currentPage;
        let filtr = filter;
        if(!!parsed.page) page=Number(parsed.page);
        if(!!parsed.term) filtr={...filtr, name:parsed.term as string};
        if(!!parsed.friend) filtr={...filtr, friend:parsed.friend==='undefined'?undefined:parsed.friend==='true'?true:false};
        dispatch(getUsersThunkCreator(page,pageSize,filtr ));
    },[]);// пустая зависимость означает сделать что-то когда компонента вмонтировалась ( componentDidMount())

    //синхронизация поиска с адресной строкой браузера
    useEffect(()=>{
        const query:any={};

        if(!!filter.name) query.name=filter.name;
        if(filter.friend) query.friend= String(filter.friend);
        if(currentPage!==1) query.page=currentPage;
            history.push({
                pathname:'/users',
                //`?term=${filter.name}&friend=${filter.friend}&page=${currentPage}`
                search: queryString.stringify(query)
            })
    },[filter, currentPage]);




    const onPageChange = (pageNumber:number) =>{
        //const {pageSize, filter} = this.props;
        // this.props.getCurrentPage(pageNumber);
        dispatch(getUsersThunkCreator(pageNumber,pageSize,filter));
    };
    const onFilterChange = (filter:filterType) => {
        dispatch (getUsersThunkCreator(1,pageSize,filter));
        console.log(filter.name)
    };
    const follow = (id:number)=>{
        dispatch(followThunkCreator(id));
    };
    const unFollow = (id:number)=>{
        dispatch(unFollowThunkCreator(id));
    };

    return(
        <div className={classes.usersPage}>
            <h2>Все зарегистрированные пользователи</h2>
         <Counter userCount = {userCount}/>
         <h3>Поиск по имени </h3>
            <SearchFormik search={onFilterChange} />
         <Pagination
               userCount = {userCount}
               pageSize = {pageSize}
               onPageChange = {onPageChange}
               currentPage = {currentPage}
           />
            {isFetching ? <Preloader/> : null}
            <OneUser
                 users = {users}
                 isFollowingProgress = {isFollowingProgress}
                 unFollowThunkCreator = {unFollow}
                 followThunkCreator = {follow}
            />
            <Pagination
                userCount = {userCount}
                pageSize = {pageSize}
                onPageChange = {onPageChange}
                currentPage = {currentPage}
            />
        </div>
    )
};
