import React from "react";
import classes from './Navblock.module.css';
import {NavLink} from "react-router-dom";
import cn from "classnames";
import Button from "../../Button/Button";
import {Avatar} from "antd";
import { UserOutlined } from '@ant-design/icons';

type PropsType ={
    LogoutThunkCreator:()=>void
    isAuth: boolean,
    login: string|null
}


const Navblock:React.FC<PropsType> = ({isAuth, login, LogoutThunkCreator }) =>{

    return(
            <div className={classes.block}>
                <div className={cn (classes.itemsSize, classes.item1)}>
                    <span><NavLink className={classes.link} to='/register'> </NavLink> </span>
                </div>
                <div className={cn (classes.itemsSize, classes.item2)}>
                    {isAuth
                        ? <div>
                            <Avatar alt={login||''} style={{ backgroundColor: 'transparent' }} icon={<UserOutlined />} />
                            <br/><span className={classes.login}> {login}</span>
                             <Button  value={"Выход"} onClick={LogoutThunkCreator}/>
                          </div>
                        : <NavLink className={classes.link} to='/login'>Вход</NavLink>}

                </div>
                <div className={cn (classes.itemsSize, classes.item3)}>
                   <span><NavLink className={classes.link} to={`/profile`}> профиль </NavLink></span>
                </div>
            </div>
    );
};

export  default Navblock;