import React from "react";
import { Link, withRouter } from 'react-router-dom';
import { Breadcrumb } from 'antd';
import { HomeOutlined} from '@ant-design/icons';
import classes from './breadCrumbs.module.css';

const breadcrumbNameMap = {
    '/': 'HOME',
    '/users': 'USERS',
    '/chat': 'CHAT',
    '/profile': 'PROFILE'
};
export const BreadCrumbs = withRouter(props => {
    const { location } = props;
    const pathSnippets = location.pathname.split('/').filter(i => i);
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
        const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
        return (
            <Breadcrumb.Item key={url}>
                <Link to={url} style={{fontSize: "10px", fontWeight: 'bold', textDecoration: "underline"}}>{breadcrumbNameMap[url]}</Link>
            </Breadcrumb.Item>
        );
    });
    const breadcrumbItems = [
        <Breadcrumb.Item key="home">
            <HomeOutlined />
            <Link to="/">Home</Link>
        </Breadcrumb.Item>,
        <Breadcrumb.Item key="users">
            <Link to="/users">users</Link>
        </Breadcrumb.Item>,
        <Breadcrumb.Item key="chat">
            <Link to="/chat">chat</Link>
        </Breadcrumb.Item>,
        <Breadcrumb.Item key="profile">
            <Link to="/profile">profile</Link>
        </Breadcrumb.Item>,
    ].concat(extraBreadcrumbItems);
    return (
        <div className={classes.bread_crumbs}>
            <Breadcrumb>{breadcrumbItems}</Breadcrumb>
        </div>
    );
});

